<template>
  <div>
    <div class="bg">
      <div class="title">购买记录</div>
      <div style="padding-bottom:20px;">
        <Input
          v-model="seachConfig.orderCode"
          placeholder="订单号"
          style="width: 200px;margin-right:10px;"
        />
        <Button icon="md-search" type="primary" @click="getData()">搜 索</Button>
      </div>
      <div class="list">
        <div>
          <Table :columns="columns1" :data="data">
            <template slot-scope="{ row, index }" slot="orderCode">{{row.orderCode}}</template>
            <template slot-scope="{ row, index }" slot="goodsName">{{row.goodsName}}</template>
            <template slot-scope="{ row, index }" slot="actualPrice">{{row.actualPrice}}元</template>
            <template
              slot-scope="{ row, index }"
              slot="status"
            >{{row.status == 0 ? '待支付' : row.status == 1 ? '支付成功' : row.status == 2 ? '支付失败' : '未知状态'}}</template>
            <template
              slot-scope="{ row, index }"
              slot="type"
            >{{row.tags == 1 ? '购买' : row.tags == 2 ? '系统赠送' : '未知状态'}}</template>
            <template slot-scope="{ row, index }" slot="createTime">{{row.createTime}}</template>
          </Table>
        </div>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Icon, Button, Page, Tag, Table, Input } from "iview";
import { getExpenseSearch } from "../../../api/record";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Table.name]: Table,
    [Input.name]: Input,
    [Tag.name]: Tag,
    [Button.name]: Button
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        orderCode: "",
        type: 0,
        pageNo: 1, //当前页
        pageSize: 10, //煤业条数
        pageTotal: 1 //总条数
      },
      data: [],
      columns1: [
        {
          title: "订单号",
          slot: "orderCode",
          width: 220
        },
        {
          title: "商品名称",
          slot: "goodsName",
          width: 150
        },
        {
          title: "价格",
          slot: "actualPrice"
        },
        {
          title: "类型",
          slot: "type"
        },
        {
          title: "状态",
          slot: "status"
        },
        {
          title: "创建时间",
          slot: "createTime",
          width: 150
        }
      ]
    };
  },
  methods: {
    getData() {
      getExpenseSearch(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    }
  },
  created() {
    this.getData();
  }
};
</script>
<style lang="less" scoped>
.bg {
  min-height: 600px;
  background: #ffffff;
  padding: 20px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    margin-bottom: 20px;
    color: #2868d1;
    font-size: 16px;
  }
  .list {
    .ivu-table-header {
      overflow: hidden;
      font-size: 14px;
      font-weight: 400;
    }
    .page {
      margin-top: 30px;
      padding-bottom: 60px;
      width: 100%;
      text-align: center;
    }
  }
}
</style>